html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

input,
select,
textarea,
button {
  font-family: inherit;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

body {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  height: auto;
}

.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 24px;
  line-height: 150%;
  color: #a4a4a4;
}

.header {
  position: sticky;
  background: #fff;
  top: 0;
  transition: all 0.3s ease;
  opacity: 1;
}
.header--in {
  box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1);
}
.header--out {
  transform: translateY(-100%);
}

.header__wrapper {
  max-width: 1290px;
  margin: 0 auto;
}

.main-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-top: 45px;
  padding-bottom: 35px;
}

.main-nav__button .main-nav__icon {
  stroke: #414141;
}
.main-nav__button .main-nav__icon-cross {
  display: none;
}
.main-nav__button .main-nav__icon-menu {
  display: block;
}
.main-nav__button--open .main-nav__icon-cross {
  display: block;
}
.main-nav__button--open .main-nav__icon-menu {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
.main-nav__list--open {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    padding-top: 20px;
  }
}

.main-nav__sublist {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  padding-right: 26px;
  padding-bottom: 30px;
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
}
.main-nav__item:last-child {
  padding-right: 0;
}
.main-nav__item:hover {
  background-image: url("../images/stroke.svg");
  background-position: center 70%;
  background-size: contain;
  background-repeat: no-repeat;
}
.main-nav__item:hover .main-nav__link {
  color: #414141;
}
.main-nav__item:hover .main-nav__sublist {
  display: block;
  min-width: 183px;
  position: absolute;
  left: 0;
  top: 100%;
  background-color: #fff;
}

.main-nav__subitem {
  border: 1px solid #414141;
  border-top: none;
}
.main-nav__subitem:first-child {
  border-top: 1px solid #414141;
}

.main-nav__sublink {
  display: block;
  padding: 8px 5px 7px 13px;
  font-size: 14px;
  line-height: 17px;
  color: #a4a4a4;
  transition: all 0.3s ease;
}
.main-nav__sublink:hover {
  background-color: #d9b696;
  color: #414141;
}

.main-nav__link {
  color: #a4a4a4;
  transition: all 0.3s ease;
}

.main-nav__user-block {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-left: 62px;
}

.promo {
  max-width: 1820px;
  margin: 0 auto;
  background: url("../images/promo/promo-bg-1.png"), url("../images/promo/promo-bg-2.png"), url("../images/promo/promo-bg-3.png"), url("../images/promo/promo-bg-4.png");
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
  background-position: top left, top right, bottom left, bottom center;
  background-size: auto;
}

.promo__wrapper {
  max-width: 1290px;
  margin: 0 auto;
  padding-top: 290px;
  padding-bottom: 313px;
}

.promo__title {
  margin: 0;
  font-weight: 300;
  font-size: 64px;
  line-height: 78px;
  letter-spacing: 0.095em;
  color: #414141;
}
.promo__title::first-letter {
  font-family: "classica_oneregular";
  font-size: 130px;
  line-height: 100px;
  letter-spacing: 0.12em;
}

.promo__text {
  max-width: 740px;
  font-size: 24px;
  line-height: 30px;
}

.search {
  align-self: center;
  margin: 0 21px;
}
.search input {
  display: none;
}

.search__btn {
  background-color: #fff;
  border: none;
}

.lang {
  margin-right: 20px;
}

.lang__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 53px;
  border: 2px solid #a4a4a4;
  color: #a4a4a4;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
}

.donate__link {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  column-gap: 20px;
  min-width: 190px;
  padding-left: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #3b8f9e;
  color: #fff;
  font-size: 20px;
  line-height: 23px;
}

.blind-version {
  align-self: center;
}

.mission {
  background-image: url("../images/mission-bg.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: left 94px;
  padding-top: 668px;
}

.mission__wrapper {
  max-width: 1290px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.mission__title {
  margin: 0;
  font-weight: 300;
  font-size: 64px;
  line-height: 78px;
  letter-spacing: 0.095em;
  color: #414141;
  width: 100%;
  margin-bottom: 40px;
  padding-right: 14%;
  text-align: right;
}
.mission__title::first-letter {
  font-family: "classica_oneregular";
  font-size: 130px;
  line-height: 100px;
  letter-spacing: 0.12em;
}

.mission__link {
  align-self: flex-end;
  font-size: 20px;
  line-height: 38px;
  color: #3b8f9e;
}
.mission__link svg {
  margin-left: 10px;
}

.mission__text {
  max-width: 740px;
  margin-left: auto;
  font-size: 24px;
  line-height: 38px;
}
.mission__text p:first-child {
  margin-top: 0;
}
.mission__text p:last-child {
  margin-bottom: 0;
}